	import selected from '@/static/mbcImg/images/business/home/personCenter/勾选-选中.png'
	import select from '@/static/mbcImg/images/business/home/personCenter/勾选-未选择.png'
export default {
	name: 'membershipAgreement',
	components: {},
	props: ['userDatas'],
	data() {
		return {
			selected:selected,
			select:select,
			vipSelect:true,//单选未选中  当vipSelect为false是说明用户选中，可以提交了
			vipSelected:false,//单选已经选中
		};
	},
	created() {},
	methods: {
		selectImg(){
			if(this.vipSelect == true){
				this.vipSelect = false,
				this.vipSelected = true
			}else{
				this.vipSelect = true,
				this.vipSelected = false
			}
			console.log(this.vipSelected);
			this.$emit('isvipSelected',this.vipSelected);
		}
	}
};
